import type { Metadata } from "next";
import { notFound } from "next/navigation";
import Link from "next/link";
import { STATE_CODES, getStateByAbbr } from "@/data/states";
import { getCitiesByState } from "@/data/cities";
import { RoundSkyWidget } from "@/components/round-sky-widget";
import { TcpaConsent } from "@/components/compliance/tcpa-consent";
import { StateQuickFacts } from "@/components/states/quick-facts";
import { HowItWorks } from "@/components/states/how-it-works";
import { FaqList } from "@/components/faq/faq-list";

export const dynamicParams = false;

export function generateStaticParams() {
  return STATE_CODES.map((code) => ({ state: code }));
}

export async function generateMetadata({ params }: { params: Promise<{ state: string }> }): Promise<Metadata> {
  const { state: stateParam } = await params;
  const state = getStateByAbbr(stateParam);
  if (!state) return { title: "Estado no encontrado" };

  const title = `Préstamos en ${state.name}`;
  return {
    title,
    description: `Consulta límites, APR y reglas de préstamos en ${state.name}.`,
    alternates: {
      canonical: `https://www.loanaiportal.com/es/states/${state.abbr.toLowerCase()}`,
    },
  };
}

export default async function StateEsPage({ params }: { params: Promise<{ state: string }> }) {
  const { state: stateParam } = await params;
  const state = getStateByAbbr(stateParam);
  if (!state) return notFound();
  const cities = getCitiesByState(state.abbr);

  return (
    <div className="mx-auto max-w-6xl space-y-16 px-4 py-16 lg:px-8">
      <nav className="text-sm text-slate-500" aria-label="Breadcrumb">
        <ol className="flex items-center gap-2">
          <li>
            <Link href="/es" className="hover:text-slate-700">
              Inicio
            </Link>
          </li>
          <li aria-hidden>/</li>
          <li>
            <Link href="/es/states" className="hover:text-slate-700">
              Estados
            </Link>
          </li>
          <li aria-hidden>/</li>
          <li className="text-slate-900 font-semibold">{state.name}</li>
        </ol>
      </nav>

      <header className="space-y-6">
        <h1 className="text-4xl font-semibold text-slate-900">Préstamos en {state.name}</h1>
        <p className="max-w-3xl text-lg text-slate-600">
          Información educativa sobre préstamos a plazos, personales y alternativas de payday en {state.name}. Verifica con el prestamista o regulador estatal antes de firmar.
        </p>
      </header>

      <div className="grid gap-10 lg:grid-cols-[1.1fr_0.9fr]">
        <div className="space-y-10">
          <article className="space-y-6 rounded-3xl border border-slate-200 bg-white/80 p-8 shadow-sm">
            <h2 className="text-2xl font-semibold text-slate-900">Requisitos comunes</h2>
            <ul className="grid gap-3 text-sm text-slate-600">
              {[
                "Ser mayor de 18 años (revisa excepciones locales)",
                "Ingresos verificables",
                "Cuenta bancaria activa",
                "Documento de identidad válido",
              ].map((item) => (
                <li key={item} className="flex items-start gap-3 rounded-2xl bg-white/70 p-4 shadow-inner">
                  <span className="mt-1 size-2 rounded-full bg-blue-400" aria-hidden />
                  <span>{item}</span>
                </li>
              ))}
            </ul>
          </article>

          <article className="space-y-6 rounded-3xl border border-slate-200 bg-white/80 p-6 shadow-sm">
            <h2 className="text-2xl font-semibold text-slate-900">Calculadora</h2>
            <div className="rounded-3xl border border-white/70 bg-white/95 p-4 shadow-inner">
              <RoundSkyWidget intent={`state-es-${state.abbr.toLowerCase()}`} pageId={`state-es-${state.abbr.toLowerCase()}`} />
            </div>
            <TcpaConsent locale="es" privacyHref="/es/privacy" termsHref="/es/terms" />
          </article>
        </div>
        <aside className="space-y-8">
          <StateQuickFacts state={state} locale="es" />
          <HowItWorks locale="es" />
          {cities.length > 0 && (
            <section className="rounded-3xl border border-slate-200 bg-white/80 p-6 shadow-sm">
              <h2 className="text-xl font-semibold text-slate-900">Ciudades destacadas</h2>
              <ul className="mt-4 space-y-3 text-sm text-slate-600">
                {cities.map((city) => (
                  <li key={city.slug}>
                    <Link
                      href={`/es/cities/${city.stateAbbr.toLowerCase()}/${city.slug}`}
                      className="flex items-center justify-between rounded-2xl bg-white/70 px-4 py-3 hover:bg-blue-50"
                    >
                      <span>
                        {city.city}, {city.stateAbbr}
                      </span>
                      <span className="text-xs text-blue-500">
                        {city.featuredIntents.join(", ")}
                      </span>
                    </Link>
                  </li>
                ))}
              </ul>
            </section>
          )}
        </aside>
      </div>

      <FaqList locale="es" />
    </div>
  );
}
